import { useState } from 'react';

// Header
export const useToggleDrawer = () => {

  // 
  const [isOpen, setIsOpen] = useState(false);

  // 
  const toggleDrawer = (open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }
    // state
    setIsOpen(open);
  };

  return { isOpen, toggleDrawer };
}